import React, { useState } from "react";
import styled, { css } from "styled-components/macro";
import { Link } from "react-router-dom";
import { MenuData } from "../Data/NavData";
import { Button } from "./Button";
import { HiMenuAlt3 } from "react-icons/hi";

function Navbar({ toggle }) {
  const [navbar, setNavbar] = useState(false);
  const Changebg = () => {
    if (window.scrollY >= 100) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };
  window.addEventListener("scroll", Changebg);

  return (
    <Nav navbar={navbar}>
      <Logo> Excello </Logo>
      <Menubar onClick={toggle} />
      <NavMenus to="/">
        {MenuData.map((menu, index) => (
          <NavMenuItems key={index} to={menu.Link}>
            {menu.title}
          </NavMenuItems>
        ))}
      </NavMenus>
      <Navbutton>
        <Button to="/contact us" primary="true">
          contact us
        </Button>
      </Navbutton>
    </Nav>
  );
}

const Nav = styled.div`
  height: 60px;
  background: ${({ navbar }) => (navbar ? "#FFBF00" : "transparent")};
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  z-index: 100;
  transition: 0.3s ease;
`;
const Navlink = css`
  color: #fff;
  display: flex;
  align-items: center;
  padding: 0 15px;
  cursor: pointer;
  height: 100%;
  text-decoration: none;

  &:hover {
    transform: scale(1.2);
    transition: 0.2s ease-in-out;
  }
`;
const Logo = styled(Link)`
  font-style: italic;
  font-weight: 1000;
  ${Navlink};
`;
const Menubar = styled(HiMenuAlt3)`
  display: none;

  @media screen and (max-width: 768px) {
    display: block;
    color: white;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(-50%, 60%);
    font-size: 25px;
    z-index: 100;
  } ;
`;
const NavMenus = styled.div`
  display: flex;
  align-items: center;
  text-transform: capitalize;
  margin-right: -48px;
  @media screen and (max-width: 768px) {
    display: none;
  } ;
`;
const NavMenuItems = styled(Link)`
  ${Navlink}
`;
const Navbutton = styled(Link)`
  display: flex;
  align-items: center;
  margin-right: 24px;
  text-decoration: none;
  @media screen and (max-width: 768px) {
    display: none;
  }
`;

export default Navbar;
