import React from "react";
import styled, { css } from "styled-components";
import { Button } from "./Button";
import { HomeDataOne } from "../Data/HomeinfoData";
import Fade from "react-reveal/Fade";

// import Jump from "react-reveal/Jump";

function HomeInfo() {
  return (
    <Section>
      <Fade left>
        <h1>view more modern home</h1>
      </Fade>

      <Container>
        {HomeDataOne.map((item, index) => (
          <ColumnRight key={index}>
            <Fade left>
              <img src={item.image} alt="home" />
            </Fade>
         <Textwrap>
         <p>{item.title}</p>
            <Button primary='false'>{item.btnLabel}</Button>
         </Textwrap>
            
          </ColumnRight>
        ))}
      </Container>
    </Section>
  );
}
const Section = styled.section`
  width: 100%;
  height: 100%;
  ${"" /* padding: 1rem 5rem; */}
  h1 {
    text-align:start;
    margin:0 0 0 10rem;
    @media screen and (max-width: 768px) {
      margin:0 0 0 2rem;
  }
  }
`;
const Container = styled.div`
  padding: 3rem calc((100vw - 1300px) / 2);

  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 600px;
  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;
const HomeImg = css`
  max-width: 500px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin-right: 1rem;
`;

const ColumnRight = styled.div`
  ${HomeImg}
  p{
    margin:1rem 0;
    
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;
const Textwrap = styled.div`
 display;flex;
 flex-direction:column;
 align-items:flex-start;
`
export default HomeInfo;
