import React, { useEffect, useRef, useState } from "react";
import styled, { css } from "styled-components/macro";
import { SliderData } from "../Data/SliderData";
// import Fade from "react-reveal/Fade";
import { Button } from "./Button";
import { IoMdArrowRoundForward } from "react-icons/io";
import { IoArrowForward, IoArrowBack } from "react-icons/io5";

function Herosection({ slider }) {
  const [current, setCurrent] = useState(0);
  const length = slider.length;
  const Timeout = useRef(null);
  // console.log(length);

  const NextSlide = () => {
    setCurrent((current) => (current === length - 1 ? 0 : current + 1));
    console.log(current);
  };
  const PrevSlide = () => {
    setCurrent((current) => (current === 0 ? length - 1 : current - 1));
    console.log(current);
  };
  useEffect(() => {
    const NextSlide = () => {
      setCurrent((current) => (current === length - 1 ? 0 : current + 1));
    };
    Timeout.current = setTimeout(NextSlide, 3000);
    return function () {
      if (Timeout.current) {
        clearTimeout(Timeout.current);
      }
    };
  }, [length, current]);

  return (
    <Hero>
      <HeroWrapper>
        {SliderData.map((slider, index) => {
          return (
            <Heroslide key={index}>
              {current === index && (
                <Heroslider>
                  <Heroimage src={slider.image} alt={slider.alt} />
                  <HeroContent>
                    <h1>{slider.title}</h1>

                    <p>{slider.price}</p>
                    <Button
                      to={slider.path}
                      primary="true"
                      css={`
                        max-width: 160px;
                      `}
                    >
                      {slider.label}
                      <Arrow />
                    </Button>
                  </HeroContent>
                </Heroslider>
              )}
            </Heroslide>
          );
        })}
        <Sliderbutton>
          <PrevArrow onClick={PrevSlide} />
          <NextArrow onClick={NextSlide} />
        </Sliderbutton>
      </HeroWrapper>
    </Hero>
  );
}
const Hero = styled.section`
  height: 100vh;
  max-height: 1100px;
  position: relative;
  overflow: hidden;
`;
const HeroWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
`;
const Heroslide = styled.div`
  z-index: 1;
  width: 100%;
  height: 100%;
`;
const Heroslider = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  &:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.4) 0%,
      rgba(0, 0, 0, 0.5) 50%,
      rgba(0, 0, 0, 0.6) 100%
    );
    z-index: 2;
    opacity: 0.7;
  }
`;
const Heroimage = styled.img`
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  object-fit: center;
`;
const HeroContent = styled.div`
  position: relative;
  z-index: 10;
  width: calc(100% - 100px);
  display: flex;
  flex-direction: column;
  max-width: 1600px;
  color: #fff;
  align-items: flex-start;

  h1 {
    font-size: clamp(1rem, 8vw, 2rem);
    text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
    font-weight: 700;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 0.8rem;
    @media (max-width: 425px) {
      font-size: clamp(0.5rem, 8vw, 1.3rem);
    }
  }
  p {
    text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
    margin-bottom: 0.8rem;
  }
`;
const Arrow = styled(IoMdArrowRoundForward)`
  margin-left: 1rem;
`;
const ArrowButton = css`
  width: 50px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  background: #000d1a;
  border-radius: 50px;
  padding: 10px;
  margin-right: 1rem;
  user-select: none;
  transition: 0.3s;
  &:hover {
    background: #cd853f;
    transform: scale(1.05);
  }
`;
const Sliderbutton = styled.div`
  display: flex;
  position: absolute;
  bottom: 50px;
  right: 50px;
  z-index: 10;
`;
const PrevArrow = styled(IoArrowBack)`
  ${ArrowButton};
`;
const NextArrow = styled(IoArrowForward)`
  ${ArrowButton};
`;

export default Herosection;
