import React from "react";
import styled, { css } from "styled-components";
import { AiOutlineInstagram } from "react-icons/ai";
import { FaFacebookF } from "react-icons/fa";
import { AiOutlineMail } from "react-icons/ai";
import { AiFillLinkedin } from "react-icons/ai";
import Fade from "react-reveal/Fade";

function footer() {
  return (
    <FooterContainer>
      <FooterWrapper>
        <Footerone>
          <Fade left>
            <h1>Let's find your dream Home</h1>
          </Fade>
        </Footerone>
        <FooterTwo>
          <h3>contact us</h3>
          <li>faq</li>
          <li>support</li>
          <li>question</li>
        </FooterTwo>
        <FooterThree>
          <h3>office</h3>
          <li>india</li>
          <li>united state</li>
          <li>europe</li>
        </FooterThree>
        <Icons>
          <Instagram />
          <Facebook />
          <Mail />
          <Linkdin />
        </Icons>
      </FooterWrapper>
    </FooterContainer>
  );
}
const FooterContainer = styled.section`
  overflow: hidden;
  height: 300px;
`;
const FooterWrapper = styled.div`
  width: 100%;
  height: 100%;
  background: #242424;
  display: grid;
  grid-template-columns: 1fr 200px 200px;
  align-items: center;
  padding-right: 3rem;
  @media (max-width: 940px) {
    grid-template-columns: 1fr;
  }
`;
const ThreeFooter = css`
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: lowercase;
`;
const Footerone = styled.div`
  ${ThreeFooter}

  h1 {
    font-size: 55px;
    color: #fff;
    max-width: 450px;
    text-align: start;
    @media (max-width: 500px) {
      max-width: 300px;
      font-size: 50px;
    }
  }
`;
const FooterTwo = styled.div`
  color: #fff;
  padding: 1rem;
  @media (max-width: 940px) {
    display: none;
  }
`;
const FooterThree = styled.div`
  color: #fff;
  padding: 1rem;
  @media (max-width: 940px) {
    display: none;
  }
`;
const Icons = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;
const IconColor = css`
  color: orange;
  font-size: 30px;
  padding: 10px;
`;
const Instagram = styled(AiOutlineInstagram)`
  ${IconColor}
`;
const Facebook = styled(FaFacebookF)`
  ${IconColor}
`;
const Mail = styled(AiOutlineMail)`
  ${IconColor}
`;
const Linkdin = styled(AiFillLinkedin)`
  ${IconColor}
`;
export default footer;
